import { PipeTransform, Pipe } from "@angular/core";

@Pipe({
    name: 'wordFilter',
    pure: false
})
export class wordFilterPipe implements PipeTransform{
    transform(words: any[], search:string): any{
        
        if(!words || !search){
            return words;
        }

        return words.filter(element=>
           element.word.toLowerCase().indexOf(search.toLowerCase()) !== -1            

        );
    }
}